<template>
    <v-container>
        <v-layout
                wrap
                justify-center
                align-center
        >
            <v-flex
                    xs12
                    sm7
                    md6
                    lg6
            >
                <v-card class="text-center mt-10 pb-2">
                    <h2 class="pt-5 mb-3">ລົງທະບຽນ</h2>
                        <v-layout
                                wrap
                                justify-center
                                align-center>
                            <v-flex  xs11 sm10 md10 lg10 class="mb-5">
                            <div  @click="google" id="customBtn" class="customGPlusSignIn " style="width: 100%">
                                <span class="icon"></span>
                                <span class="buttonText">ເຂົ້າສູ່ລະບົບດ້ວຍ Google</span>
                            </div>
                             </v-flex>
                            <v-flex  xs11 sm10 md10 lg10>
                            <a @click="OnFacebookAuthSuccess"  v-facebook-signin-button="appId" style="width: 100%"
                                class="fb connect ">ເຂົ້າສູ່ລະບົບດ້ວຍ Facebook</a>
                            </v-flex>
                        <v-flex xs11 sm10 md10 lg10>
                            <h3 class="my-3">ຫຼື</h3>
                            <validation-observer slim ref="observer">
                                <v-form @submit.prevent="signIn" class="text-center">
                                    <validation-provider
                                            mode="lazy"
                                            rules="required|between: 4, 25"
                                            v-slot="{ errors }"
                                    >
                                        <v-text-field
                                                v-model="email"
                                                prepend-inner-icon="face"
                                                color="info"
                                                label="ຊື່ຜູ້ໃຊ້ / ອີເມວ"
                                                outlined
                                                type="text"
                                                clearable
                                                :error-messages="errors"
                                        ></v-text-field>
                                    </validation-provider>
                                    <validation-provider
                                            name="password"
                                            mode="lazy"
                                            rules="required|between: 6, 20"
                                            v-slot="{ errors }"
                                    >
                                        <v-text-field
                                                v-model="password"
                                                prepend-inner-icon="lock"
                                                label="ລະຫັດຜ່ານ"
                                                outlined
                                                type="password"
                                                clearable
                                                :error-messages="errors"
                                        ></v-text-field>
                                    </validation-provider>
                                    <v-btn type="submit" block large class="primary">ລົງທະບຽນ</v-btn>
                                </v-form>
                            </validation-observer>
                        </v-flex>
                    </v-layout>
                    <div>
                        <p class="mt-5">ບໍ່ມີບັນຊີຜູ້ໃຊ້? <router-link :to="{ name: 'SignUp' }" style="text-decoration:
                        none;"
                        >ສະໝັກສະມາຊິກ
                        </router-link> </p>
                    </div>
                </v-card>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
    import { ValidationProvider, extend } from 'vee-validate';
    import { required } from 'vee-validate/dist/rules';
    import { ValidationObserver } from 'vee-validate'
    import { mapActions } from 'vuex'

    import FacebookSignInButton from 'vue-facebook-signin-button-directive'

    extend('required', {
        ...required,
        message: 'ກະລຸນາປ້ອນຂໍ້ມູນໃນ {_field_} ນີ້'
    })
    extend('between', {
        validate: (value, { min, max }) => {
            const length = value && value.length;
            return length >= min && length <= max;
        },
        params: ['min', 'max'],
        message: 'ຂໍ້ມູນ {_field_} ຕ້ອງມີຄວາມຍາວລະຫວ່າງ {min} ຫາ {max} ຕົວອັກສອນ'
    })
    export default {
        components: {
            ValidationProvider,
            ValidationObserver
        },
        data: () => ({
            appId: '520207398815072',
            email: '',
            password: ''
        }),
        directives: {
            FacebookSignInButton
        },
        methods: {
            ...mapActions([
               'googleSignIn'
            ]),
            // Google SignIn
            async google () {
              try {
                  await this.googleSignIn()
                  this.$router.push({ name: 'Home'})
                  this.$swal({
                      title: 'ເຂົ້າສູ່ລະບົບຜ່ານທາງ Google ແລ້ວ',
                      type: 'success',
                      toast: true,
                      position: 'top-end',
                      showConfirmButton: false,
                      timer: 2000,
                  })
              }  catch (err) {
                  return
                 // throw new Error(err)
              }
            },
            // FACEBOOK SignIn
            async OnFacebookAuthSuccess (idToken) {
              try {
                  const access_token = await idToken
                  await this.$store.dispatch('facebookSignIn', access_token )
                  this.$swal({
                      title: 'ເຂົ້າສູ່ລະບົບຜ່ານທາງ Facebook ແລ້ວ',
                      type: 'success',
                      toast: true,
                      position: 'top-end',
                      showConfirmButton: false,
                      timer: 2000,
                  })
                  this.$router.push({ name: 'Home' })
              }catch (err) {
                  return
                //  throw new Error(err)
              }
            },
            OnFacebookAuthFail (e) {
                throw e
            },
            // Local SignIn
             async signIn () {
                    const isValid = await this.$refs.observer.validate()
                    if(isValid) {
                       try {
                           await this.$store.dispatch('signIn', {
                               email: this.email,
                               password: this.password
                           })
                           this.$router.push({ name: 'Home' })
                           this.$swal({
                               title: 'ເຂົ້າສູ່ລະບົບແລ້ວ',
                               type: 'success',
                               toast: true,
                               position: 'top-end',
                               showConfirmButton: false,
                               timer: 2000,
                           })
                       } catch (err) {
                           this.$swal({
                               title: 'ຊື່ຜູ້ໃຊ້ ຫຼື ລະຫັດຜ່ານບໍ່ຖືກຕ້ອງ',
                               type: 'error'
                           })
                           throw err
                    }
                    }
            }
        }
    }
</script>

